import Modal from "react-bootstrap/Modal";
import "Menus.css";
import MenuItemComponent from "./MenuItemComponent";
import * as React from "react";
import infoIcon from 'assets/icon_info.svg';
import nukeIcon from 'assets/icon_nuke.png';
import passIcon from 'assets/icon_pass.png';
import resetIcon from 'assets/icon_reset.png';
import resignIcon from 'assets/icon_resign.svg';
import tileBagIcon from 'assets/icon_tileBag.svg';
import tile from "tile.svg";
import {RulesPanelKey} from "./RulesComponent";

interface IMainMenuProps {
    show: boolean;
    hide(callback: () => void): void;

    help(section: RulesPanelKey): void;

    canReset: boolean;
    reset(): void;

    usedNuke: boolean;
    noMoreTiles: boolean;
    nuke(): void;

    canPass: boolean;
    passWillEndGame: boolean;
    pass(): void;
}

export default class MainMenuComponent extends React.Component<IMainMenuProps> {
    public render() {
        const {show, hide,

            help,

            canReset, reset,
            canPass, passWillEndGame, pass,

            usedNuke, noMoreTiles, nuke} = this.props;

        let nukeTitle = "Discard all tiles and draw new ones. You may only do this once per game.";
        if (usedNuke) {
            nukeTitle = "You may only use the nuclear option once per game.";
        } else if (noMoreTiles) {
            nukeTitle = "It’s too late to use the nuclear option.";
        }

        return <Modal className="menu main-menu" show={show} onHide={() => hide(() => {/**/})} backdrop={true}>
            <Modal.Body>
                <MenuItemComponent
                    title={"How to Play"}
                    description={"Getting started and rules of the game"}
                    icon={infoIcon}
                    onClick={() => hide(() => help(RulesPanelKey.howToPlay))}
                />

                <MenuItemComponent
                    title={"Dictionary"}
                    description={"Information about which words are acceptable"}
                    icon={tileBagIcon}
                    onClick={() => hide(() => help(RulesPanelKey.dictionary))}
                />

                <MenuItemComponent
                    title={"Tile Distribution"}
                    description={"How many tiles there are of each letter and how many remain in the bag"}
                    icon={tileBagIcon}
                    onClick={() => hide(() => help(RulesPanelKey.helpTiles))}
                />

                <MenuItemComponent
                    title={"Reset"}
                    description={"Return tiles to their original positions, restoring their point values."}
                    icon={resetIcon}
                    disabled={!canReset}
                    onClick={() => hide(reset)}
                />

                <MenuItemComponent
                    title={passWillEndGame ? "Pass" : "Finish"}
                    description={passWillEndGame ? "Finish the game" : "No moves? Pass your turn"}
                    icon={passIcon}
                    disabled={!canPass}
                    onClick={() => hide(pass)}
                />

                <MenuItemComponent
                    title={"Nuke"}
                    description={nukeTitle}
                    icon={nukeIcon}
                    badge={usedNuke ? 0 : 1}
                    disabled={usedNuke}
                    onClick={() => hide(nuke)}
                />

                {/*<MenuItemComponent*/}
                {/*    title={"Resign"}*/}
                {/*    description={"End this game, but it counts as a loss"}*/}
                {/*    icon={resignIcon}*/}
                {/*    disabled={true}*/}
                {/*    onClick={() => hide(resign)}*/}
                {/*/>*/}
            </Modal.Body>
        </Modal>;
    }
}
