import {ITile} from 'App';
import {GoalComponent} from 'GoalComponent';
import {IGoal} from 'Goals';
import compact from 'lodash/compact';
import {IPlay} from 'Move';
import React from 'react';
import Card from 'react-bootstrap/Card';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

export class GoalListComponent extends React.PureComponent<{
mode: "vertical" | "horizontal",
  goals: IGoal[];
  pendingMove: IPlay;
  board: ITile[];
  canPlay: boolean;
}> {
    public render() {
        switch (this.props.mode) {
            case "vertical":
                return this.renderVertical();
            case "horizontal":
                return this.renderHorizontal();
        }
    }

    private renderVertical() {
        const {goals, pendingMove: move, canPlay, board} = this.props;

        return <Card>
            <Card.Header>Goals</Card.Header>
            <Card.Body>
                {goals.map((goal, index) => {
                    const possible = goal.possible(board);
                    const thisMoveQualifies = canPlay && goal.check(move);
                    const classNames = ['goal', thisMoveQualifies && 'qualifies', possible && 'possible'];
                    const className = compact(classNames).join(' ')
                    return <GoalComponent goal={goal} id={index} key={index} className={className}
                                          mode={"horizontal"}/>;
                })}
            </Card.Body>
        </Card>;
    }

    private renderHorizontal() {
        const {goals, pendingMove: move, canPlay, board} = this.props;

        return <><Row className="goals-header">
            <Col xs={1}/>
            <Col className="hr flex-grow"/>
            <Col className="goals-title" xs={"auto"}>Goals</Col>
            <Col className="hr flex-grow"/>
            <Col xs={1}/>
        </Row>
        <Row className={"goals-horizontal"}>
                {goals.map((goal, index) => {
                    const possible = goal.possible(board);
                    const thisMoveQualifies = canPlay && goal.check(move);
                    const classNames = ['goal', thisMoveQualifies && 'qualifies', possible && 'possible'];
                    const className = compact(classNames).join(' ')
                    return <GoalComponent goal={goal} id={index} key={index} className={className}
                                          mode={"horizontal"}/>;
                })}
        </Row></>;
    }
}
