/**
 * State held in the conversation meta and thus shared between players
 */
// TODO: unflatten IAppState to make code and logic easier to follow.
interface ISharedState {
    /**
     * Represents a "reservation" of the right to change game state.  Mostly it was to help people put their names in
     * when it isn't their turn at the beginning, but may not actually be necessary if the version field is properly
     * respected (i.e. checked agains the local state after join but before edit).
     */
    baton?: {
        userId: string,
        timeout: number,
    };

    /**
     * Same as the conversation id
     */
    gameId: string;

    /**
     * Optimistic concurrency version number for {gameState}
     */
    gameStateVersion: number;

    // Would be part of this object if not flattened into {IAppState}
    // gameState: IGameState,
}

interface IGameState {
    players: IPlayer[];
    playerIndex: number;
    nukers: number[];
    passers: number[];
    over: boolean;
    announcements: IAnnouncement[];
    options: IScoochOptions;
    bag: ITile[];
    board: ITile[];
    original: ITile[];
    word: ITile[];
    reordered: ITile[];
    scooched?: string;
    smeared?: boolean;
    goals: IGoal[];
}

interface IAppState extends ISharedState, IGameState {
    loading: string;
    uiState: IUiState;
}

function playerScore(player: IPlayer) {
    return player.words.map((i) => i.type === "play" ? i.points + i.bonusPoints : 0).reduce((a, b) => (a + b), 0);
}

function getTileSet(tileSetName?: string) {
    return TileSets.find((e) => e.name === tileSetName) || TileSets[0]!;
}

function newGame(players?: IPlayer[], tileSetName?: string): IAppState {
    const board: ITile[] = [];
    const bag: ITile[] = [];
    let id = 0;
    const tileSet = getTileSet(tileSetName);
    tileSet.tiles.forEach((data) => {
        for (let j = 0; j < data.count; j++) {
            bag.push({
                type: "letter",
                id: `tile-${id++}`,
                letter: data.letter,
                points: data.points,
                drop: 0,
            });
        }
    });
    for (let j = 0; j < 16; j++) {
        board.push({
            type: "drop",
            id: `blank-${j}`,
        } as ITile);
    }
    players = players || [];
    players.forEach((player) => player.words = []);
    const options = {...DEFAULT_OPTIONS, tileSetName: tileSet.name};
    const gameId = uuid();
    return {
        uiState: {},
        loading: "bootstrap",
        gameStateVersion: 1,
        gameId,
        players,
        playerIndex: 0,
        bag,
        board,
        original: Array(...board),
        word: [],
        reordered: [],
        nukers: [],
        passers: [],
        over: false,
        options,
        goals: [],
        announcements: [],
    };
}

// Cache of generated json data URLs
const JSON_TO_URL_DATA: { [key: string]: string } = {};

function jsonToUrl(key: string, stuff: any) {
    const data = new Blob([JSON.stringify(stuff)], {type: "application/json"});

    // If we are replacing a previously generated file we need to
    // manually revoke the object URL to avoid memory leaks.
    if (JSON_TO_URL_DATA[key]) {
        window.URL.revokeObjectURL(JSON_TO_URL_DATA[key]);
    }

    JSON_TO_URL_DATA[key] = window.URL.createObjectURL(data);

    // returns a URL you can use as a href
    return JSON_TO_URL_DATA[key];
}

