import {IGoal} from 'Goals';
import React from 'react';
import Badge from 'react-bootstrap/Badge';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import Row from 'react-bootstrap/Row';
import "Goals.css";
import tile from "tile.svg";

export class GoalComponent extends React.PureComponent<{
    mode: "vertical" | "horizontal",
    goal: IGoal,
    id: number,
    className: string,
}> {
    public render() {
        switch (this.props.mode) {
            case "vertical":
                return this.renderVertical();
            case "horizontal":
                return this.renderHorizontal();
        }
    }

    private renderVertical() {
        return (
            <Row key={this.props.id} className={this.props.className}>
                <Col md={10}>
                    <Badge variant="warning">{this.props.goal.type}</Badge>
                    <br/>
                    {this.props.goal.description}
                </Col>
                <Col md={2}>
                    <Badge variant="success" pill={true}>
                        +{this.props.goal.points}
                    </Badge>
                </Col>
            </Row>
        );
    }
    private renderHorizontal() {
        return (
            <Col key={this.props.id} className={this.props.className} xs={4}>
                <div>
                <Row>
                    <div className="bonus-circle">
                        +{this.props.goal.points}
                    </div>
                </Row>
                <Row>
                <Col className="flex-grow">
                        {this.props.goal.description}
                    </Col>
                </Row>
                </div>
            </Col>
        );
    }
}
